body {
    background-color: var(--background-color);

    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Roboto", sans-serif;
    font-size: 16px;
}

/*Calculator*/

.calculator {
    font-size: 1.5rem;
    padding: 100px 15px 15px;
    display: grid;
    box-shadow: var(--calculator-shadow);
    border-radius: 40px;
    width: min-content;
}

.calculator-result {
    color: var(--color-text-primary);
    font-size: 40px;
    width: 100%;
    white-space: nowrap;
    background: none;
    border: none;
    box-shadow: none;
    outline: none;
    margin-bottom: 30px;
    text-align: right;
}

.calculator-buttons {
    display: grid;
    grid-template-columns: repeat(4, 60px);
    grid-gap: 15px;
}

.calculator-button {
    padding: 0;
    height: 60px;
    width: 60px;
    border-radius: 30px;
    background: var(--button-background);
    box-shadow: var(--button-shadow);
    color: var(--color-text-primary);
    border: none;
    cursor: pointer;
    margin: auto;
}

.calculator-button:hover {
    background: var(--button-background-hover);
    box-shadow: var(--button-shadow);
}

.calculator-button:focus {
    background: var(--button-background-focus);
    box-shadow: var(--button-shadow);
}

.calculator-button:active {
    box-shadow: var(--button-shadow-active);
}

.zero-button {
    width: 135px;
    grid-column: span 2;
}

.main-action {
    color: var(--color-text-accent);
}

.secondary-action {
    color: var(--color-text-secondary);
}

/*Theme switcher*/

.theme-switcher {
    color: var(--color-text-primary);
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 20px;
    left: 20px;
}
